@font-face {
  font-family: HelveticaFont;
  src: url("https://emperia-static.s3.eu-west-2.amazonaws.com/skin/Helvetica.ttf");
}

@font-face {
  font-family: HelveticaBold;
  src: url("https://emperia-static.s3.eu-west-2.amazonaws.com/skin/Helvetica-Bold.ttf");
}

@font-face {
  font-family: Avenir;
  src: url("../fonts/AvenirRoman.otf");
}

@font-face {
  font-family: AvenirBlack;
  src: url("../fonts/AvenirBlack.otf");
}

@font-face {
  font-family: AvenirBook;
  src: url("../fonts/AvenirBook.otf");
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: transparent;
  box-sizing: border-box;
}

.main {
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

.clickable {
  display: flex;
  justify-content: center;
  user-select: none;
  cursor: pointer;
}

.menu-svg {
  width: 13px;
  height: 13px;
  margin: auto 0;
}

.size-select-boxes {
  display: flex;
  flex-direction: row;
  align-items: left;
  justify-content: left;
}

.size-select-boxes>p{
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;

  width: 48px;
  height: 48px;
  border: 1px solid #949494;
  margin: 0 12px;

  user-select: none;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  border-radius: 5px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgb(202, 202, 202);
  outline: 1px solid rgb(160, 160, 160);
}
